<template>
  <v-app>
    <v-container fluid grid-list-lg class="blue darken-1">
      <v-layout row wrap>
        <v-flex xs12>
          <h1 class="text-xs-center white--text hq-unselectable">{{ APP_NAME }}</h1>
        </v-flex>
        <v-flex xs12>
          <input type="text" v-model="search" style="border:1px solid white;"/>
        </v-flex>
        <v-flex xs12 id="test2">
          <div v-if="is_list_loaded" id="test">
            <!-- <v-card max-width="600" class="hq-centered card mb-3" v-if="_.isEmpty(quizzes)">
              <v-card-title>Sorry quizzes are not prepared...</v-card-title>
            </v-card> -->
            <v-card max-width="600" class="hq-centered card mb-3" v-for="quiz in filteredList">
              <v-card-title primary-title class="pt-3 pb-2">
                <div>
                  <h3 class="headline mb-0">{{ quiz.name }}</h3>
                  <div>{{ quiz.description }}</div>
                  <div>{{ quiz.creator }}</div>
                </div>
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn @click="play(quiz.id)" color="green lighten-1" dark>Play</v-btn>
              </v-card-actions>
            </v-card>
            <!-- <v-card max-width="600" class="hq-centered card mb-3" v-for="index in 1000">
              <v-card-title primary-title class="pt-3 pb-2">
                <div>
                  <h3 class="headline mb-0">Test</h3>
                  <div>Test</div>
                </div>
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn @click="" color="green lighten-1" dark>Play</v-btn>
              </v-card-actions>
            </v-card> -->
          </div>
          <div v-else class="text-xs-center">
            <v-progress-circular
              indeterminate
              color="white"
            ></v-progress-circular>
          </div>
          <!-- <v-menu transition="slide-x-reverse-transition" :close-on-content-click="false">
          </v-menu> -->
          <v-btn 
              slot="activator" 
              dark 
              color="secondary" 
              fixed 
              bottom 
              right
              fab
              icon
              @click="open_dd()">
                <v-icon>search</v-icon>
              </v-btn>
              <input type="text" class="test" id="abc" style="border:3px solid white;position:fixed;bottom:30px;right:120px;">
          <!-- <v-btn fab
            style="position:fixed;bottom:50px;right:50px;"
            @click="open_dd()"
          >dd</v-btn> -->
        </v-flex>
      </v-layout>
    </v-container>
  </v-app> 
</template>
<script>
// import Clusterize from 'clusterize.js'
import HyperList from 'hyperlist'
export default {
  data () {
    return {
      quizzes: [],
      is_list_loaded: false,
      search: '',
      fab: true
    }
  },
  watch: {
    search (val) {
      window.scrollTo(0, 0);
    }
  },
  computed: {
    filteredList() {
      return this.quizzes.filter(quiz => {
        return quiz.name.toLowerCase().includes(this.search.toLowerCase()) || 
                quiz.creator.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  created () {
    this.get_quiz_list()
  },
  mounted () {
    // document.getElementById('test').style.height = `${1000}px`
  },
  methods: {
    play (quiz_id) {
      this.$router.push({
        name: 'loading_room',
        params: {
          quiz_id: quiz_id
        }
      })
    },
    get_quiz_list () {
      this.$http.get(`${this.PATH_API}/quiz/list`).then(res => {
        console.log('res', res)
        this.quizzes = res.data.result
        this.is_list_loaded = true
      }).catch(err => {
        console.log('Sever has some problems')
      })  
    },
    open_dd () {
      let a = document.querySelector("#abc")
      // a.classList.add('test')
      // console.log(a)
    }
  }
}
</script>
<style scoped>
  .card {
    border-radius: 10px;
  }

  .test { 
  animation-name: example;
    animation-duration: 4s;
    animation-iteration-count: 2;
    animation-direction: alternate-reverse;
    /*here*/
 }
</style>
<style>
  .hq-centered {
    margin-left: auto;
    margin-right: auto;
  }
  .hq-unselectable {
    -webkit-user-select: none;  /* Chrome all / Safari all */
    -moz-user-select: none;     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */   
  }

</style>